<template>
    <div id="app">
        <Fheader v-bind:title="header_title"></Fheader>
        <div class="doctor-form">
            <div class="piece">姓名</div>
            <div class="info-card">
                <input type="" name="" class="input-info" v-model="doctor_name" placeholder="请输入姓名">
            </div>
            <div class="piece">性别</div>
            <div class="info-card">
                <template v-for="(gender, index) in gender_array">
                    <div class="custom-radio" v-on:click="genderHandler(index)" v-bind:class="[ gender.selected ? 'custom-radio-filled' : '' ]">{{ gender.name }}</div>
                </template>
            </div>
            <div class="piece">所在医院</div>
            <div class="info-card">
                <input type="" name="" class="input-info" v-model="hospital" placeholder="请输入所在医院">
            </div>
            <div class="piece">所在科室</div>
            <div class="info-card">
                <input type="" name="" class="input-info" v-model="section" placeholder="请输入所在科室">
            </div>
        </div>
		<div class="ft-bar-next" v-on:click="nextHandler">下一步</div>
		<transition name="fade">
			<Toast v-bind:msg="toast_text" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>

<script>
import Fheader from '../../components/common/Fheader'
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toast_text: '',
            header_title: '医生信息',
            nextlink: 'http://www.baidu.com',
            doctor_name: '',
            hospital: '',
            section: '',
            gender_array: [
                {
                    name: '男',
                    selected: false
                },
                {
                    name: '女',
                    selected: false
                }
            ]
    	}
    },
    methods:{
        genderHandler: function(index){
            var that = this;
            for(var i=0;i<that.gender_array.length;i++){
                that.gender_array[i].selected = false;
            }
            that.gender_array[index].selected = true;
        },
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 1500);
    	},
        nextHandler: function(){
            var that = this;
            var data = that.getFormData();
            if(data.real_name == '' || data.real_name == undefined){
                that.toast_text = '请填写您的姓名！';
                that.showToast();
                return;
            }
            if(data.sex == '' || data.sex == undefined){
                that.toast_text = '请选择您的性别！';
                that.showToast();
                return;
            }
            if(data.hospital == '' || data.hospital == undefined){
                that.toast_text = '请填写所在医院！';
                that.showToast();
                return;
            }
            if(data.department == '' || data.department == undefined){
                that.toast_text = '请填写所在科室！';
                that.showToast();
                return;
            }
            store.user_info(data).then(function(data){
                that.toast_text = data.data.message;
                that.showToast();
            }).catch(function(error){
                that.toast_text = data.data.message;
                that.showToast();
            });
        },
        getFormData: function(){
            //dealData处理成form数据，data对象key对应后端接口
            var that = this;
            var data = {};
            for(var i=0;i<that.gender_array.length;i++){
                if(that.gender_array[i].selected){
                    data.sex = i == 1 ? 'F' : 'M';
                }
            }
            data.birthday = '';
            data.diagnosis_time = '';
            data.real_name = utils.trim(that.doctor_name);
            data.hospital = utils.trim(that.hospital);
            data.department = utils.trim(that.section);
            return data;
        }
    },
    components: {
    	Fheader,
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
.doctor-form{
    padding: 10px 15px 60px 15px;
}
</style>



